import React, { useState } from 'react';
import { Query, graphql } from 'react-apollo';
import { Router } from '../../routes';

import gql from 'graphql-tag';
const GET_USER = gql`
	{
		getUsers {
			_id
			username
			password
		}
	}
`;
let message = 'wrong username or password';
const LoginPortal = ({ show, client, setShow }) => {
	const [
		count,
		setCount
	] = useState(0);

	let username, password;

	const handleLogin = async () => {
		localStorage.setItem('usuario', username.value);
		localStorage.setItem('password', password.value);
		await client.resetStore();
	};

	const handleSubmit = async (e) => {
		e.preventDefault();
		await handleLogin();
		await client
			.query({
				query : GET_USER
			})
			.then((result) => {
				console.log(result);
				if (result.data.getUsers) {
					setShow(false);
					setCount(0);
				} else {
					setCount(3);
				}

				return 'console.log(result);';
			});
	};
	return (
		<div>
			<div className='portal'>
				<div className='login-container'>
					<div
						onClick={() => {
							setShow(false);
						}}
						className='exit'>
						{/* <svg
							xmlns='http://www.w3.org/2000/svg'
							viewBox='0 0 26 26'
							width='18'
							height='18'
							fill='#000000'>
							<path d='M 3 0 L 3 5 C 3.003906 5.527344 3.21875 6.035156 3.59375 6.40625 L 10.1875 13 L 3.59375 19.59375 C 3.21875 19.964844 3.003906 20.472656 3 21 L 3 26 L 7 26 L 7 21.8125 L 13 15.8125 L 19 21.8125 L 19 26 L 23 26 L 23 21 C 22.996094 20.472656 22.78125 19.964844 22.40625 19.59375 L 15.8125 13 L 22.40625 6.40625 C 22.78125 6.035156 22.996094 5.527344 23 5 L 23 0 L 19 0 L 19 4.1875 L 13 10.1875 L 7 4.1875 L 7 0 Z' />
						</svg> */}
						<svg
							xmlns='http://www.w3.org/2000/svg'
							viewBox='0 0 50 50'
							width='25'
							height='25'
							fill='#000000'>
							<path d='M 25 2 C 12.316406 2 2 12.316406 2 25 C 2 37.683594 12.316406 48 25 48 C 37.683594 48 48 37.683594 48 25 C 48 12.316406 37.683594 2 25 2 Z M 33.847656 34.707031 L 30.648438 34.707031 L 25.605469 27.105469 L 25.386719 27.105469 L 20.257813 34.707031 L 17.238281 34.707031 L 23.925781 24.78125 L 17.308594 14.980469 L 20.546875 14.980469 L 25.578125 22.675781 L 25.808594 22.675781 L 30.921875 14.980469 L 33.984375 14.980469 L 27.1875 24.878906 Z' />
						</svg>
					</div>

					<h1>Login</h1>
					<form
						action='POST'
						onSubmit={(e) => {
							handleSubmit(e);
						}}>
						<label htmlFor='username'>username:</label>
						<input
							type='text'
							ref={(input) => {
								username = input;
							}}
							placeholder='username'
						/>
						<label htmlFor='password'>password:</label>
						<input
							type='password'
							ref={(input) => {
								password = input;
							}}
							placeholder='password'
						/>
						<input className='btn' type='submit' />
					</form>

					<p className='message'>{message}</p>
				</div>
			</div>

			<style jsx>
				{`
					h1 {
						color: #db415d;
						text-transform: uppercase;
					}
					.exit {
						position: absolute;
						right: 5px;
						top: 5px;
						font-size: 1.5rem;
					}
					input {
						padding: .5em;
						border: 0;
						margin: .8em .4em;
					}
					.btn {
						border-radius: 5px;
						margin: 0 auto;
						margin-top: 3em;
						background: #db415d;
						color: white;
						text-transform: uppercase;
						font-weight: bold;
						width: 100%;
					}
					.message {
						color: red;
						font-size: 0.6rem;
						display: ${count < 1 ? 'none' : 'inline'};
					}
					.portal {
						height: 100vh;

						left: 0;
						top: 0;
						width: 100vw;
						position: fixed;
						z-index: 100000000;
						background: rgba(0, 0, 0, 0.4);
						display: ${show ? 'block' : 'none'};
					}
					.login-container {
						background: white;
						width: 300px;
						margin: 10% auto;
						padding: 30px;
						position: relative;
						height: 300px;
					}
					.exit svg:hover {
						cursor: pointer;
						fill: red;
					}
				`}
			</style>
		</div>
	);
};

export default LoginPortal;
